:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
@keyframes radar-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes radar-parallax-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
@keyframes radar-pulse {
  0% {
    transform: scale(0.4);
    opacity: 0.8;
  }
  60% {
    transform: scale(1);
    opacity: 0;
  }
  100% {
    transform: scale(1.1);
    opacity: 0;
  }
}
@keyframes radar-dot-blink {
  0%, 55%, 100% {
    opacity: 0.15;
    transform: scale(0.9);
  }
  10% {
    opacity: 1;
    transform: scale(1.1);
  }
}
@keyframes shimmer {
  0% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(220%);
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.radar-sweep {
  animation: radar-spin 3s linear infinite;
}
.radar-parallax {
  animation: radar-parallax-spin 18s linear infinite;
}
.radar-pulse {
  width: 130%;
  height: 130%;
  border-radius: 9999px;
  border: 1px solid rgba(34, 211, 238, 0.4);
  box-shadow: 0 0 35px rgba(34, 211, 238, 0.5);
  animation: radar-pulse 2.6s ease-out infinite;
}
.radar-dot {
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 9999px;
  background: rgba(56, 189, 248, 0.95);
  box-shadow: 0 0 12px rgba(56, 189, 248, 1);
  animation: radar-dot-blink 2.8s infinite;
}
.shimmer-bar {
  animation: shimmer 1.8s ease-in-out infinite;
}
.animate-fade-in {
  animation: fade-in 0.28s ease-out;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button, a, input {
  min-height: 44px;
  min-width: 44px;
}
@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
  body {
    padding: 0;
    overflow-x: hidden;
  }
  main {
    padding-bottom: env(safe-area-inset-bottom);
  }
}
@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slideDown {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(100%);
    opacity: 0;
  }
}
.sheet-open {
  animation: slideUp 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}
.sheet-close {
  animation: slideDown 0.3s ease-in forwards;
}
button:active {
  transform: scale(0.98);
}
input:active {
  transform: scale(1.01);
}
input,
textarea,
select {
  font-size: 16px;
}
* {
  transition: background-color 0.2s ease, color 0.2s ease;
}
@media (max-width: 640px) {
  .form-container {
    border-radius: 16px;
    margin: 0 12px;
  }
  button {
    padding: 12px 20px;
    font-size: 14px;
  }
  input {
    font-size: 16px;
    padding: 12px 14px;
  }
}
@media (max-width: 640px) {
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 18px;
  }
  p {
    font-size: 13px;
  }
}
nav {
  padding-top: env(safe-area-inset-top);
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}
@media (max-width: 768px) {
  .bottom-sheet {
    padding-bottom: env(safe-area-inset-bottom, 20px);
  }
}
